.video{
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 3px;
    margin: 3px;
    align-items: left;
    cursor: pointer;
}
.video:hover{
    animation-name: video-animation;
    animation-duration: 0.5s;
    animation-fill-mode: both;
}
@keyframes video-animation{
    from {opacity: 1} 
    to {opacity: 0.6}
}
.video .video_data{
    padding: 10px;
    display: flex;
}
.video .video_data .channelBtn{
    width: 40px;
    height: 40px;
    border: none; 
    background: black;
    border-radius: 50%;
    margin-right: 8px;
    padding: 0;
    cursor: pointer;
}
.video .video_data .channelBtn img{
    width: 40px;
    height: 40px;
    border-radius: 50%;
}
.video .video_data .title{
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
    overflow: hidden;
}
.video img[alt="thumbnail"]{
    width: 100%;
    height: auto;
}
.video span{
    color: rgb(230, 230, 230);
}
.video span .title{
    font-weight: 500;
    margin-bottom: 8px;
}
.video span .channelTitle{
    font-size: 14px;
    color: rgb(141, 141, 139);
}


/* Side Video */
.video.side{
    flex-direction: row;
}
.video.side img[alt="thumbnail"]{
    width: 50%;
    height: auto;
}
.video.side .video_data .channelBtn{
    display: none;
}
.video.side .video_data{
    padding-top: 0;
}
.video.side .video_data .title{
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
}